.sign-up-form {
  width: 100%;
  padding: 15px;
  margin-top: 30px;
  .inputs-style {
    padding: 20px;
  }
  .signup-register-radios {
    display: flex;
    width: 30%;
    align-items: center;
  }

  .sign-up-button-style {
    width: 100%;
  }
  .sign-up-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    flex-flow: wrap;
    width: 100%;

    .sign-in-buttons-style {
      cursor: pointer;
      color: #3d3d3d;
      &:hover {
        color: #007bff;
      }
    }
  }
}
